<template>
    <div class="vp">
        <video :src="`/${video.filePath}`" controls></video>
    </div>
</template>

<script>
import {mapActions} from 'vuex'

export default {
    name: 'VideoPage',
    data() {
        return {
            video: {}
        }
    },
    created() {
        this.fetchVideo()
    },
    methods: {
        fetchVideo() {
            const data = {
                videoId: this.$route.params.videoId
            }

            this.getVideo(data)
                .then(res => {
                    this.video = res.data.video
                })
        },
        ...mapActions(['getVideo'])
    }
}
</script>

<style lang="scss">
.vp {
    margin-top: 100px;
}
</style>